import { Link } from "gatsby"
import React from "react"

import { rhythm, scale } from "../utils/typography"

const Layout = ({ location, title, children }) => {
  const rootPath = `${__PATH_PREFIX__}/`;
  let header;

  if (location.pathname === rootPath) {
    header = (
      <h1
        style={{
          ...scale(1.5),
          margin: rhythm(0.5),
        }}
      >
        <Link
          style={{
            boxShadow: `none`,
            color: `inherit`,
          }}
          to={`/`}
        >
          {title}
        </Link>
      </h1>
    );
  } else {
    header = (
      <h3
        style={{
          ...scale(1.5),
          margin: rhythm(0.5),
        }}
      >
        <Link
          style={{
            boxShadow: `none`,
            color: `inherit`,
          }}
          to={`/`}
        >
          {title}
        </Link>
      </h3>
    );
  }
  return (
    <div>
      <header
        style={{
          marginLeft: `auto`,
          marginRight: `auto`,
          maxWidth: rhythm(30),
          background: `#222222`,
          textAlign: `center`,
          padding: `${rhythm(1)} ${rhythm(3 / 4)}`,
          marginBottom: rhythm(1.5),
          borderRadius: `0 0 ${rhythm(1)} ${rhythm(1)}`,
        }}
      >
        {header}
      </header>
      <div
        style={{
          marginLeft: `auto`,
          marginRight: `auto`,
          maxWidth: rhythm(30),
          padding: `0 ${rhythm(3 / 4)}`,
        }}
      >
        <main>{children}</main>
      </div>
      <footer
        style={{
          marginLeft: `auto`,
          marginRight: `auto`,
          maxWidth: rhythm(30),
          background: `#222222`,
          padding: `${rhythm(1.5)} ${rhythm(3 / 4)}`,
          marginTop: rhythm(1),
          textAlign: `center`,
          borderRadius: `${rhythm(1)} ${rhythm(1)} 0 0`,
        }}
      >
        &copy; {new Date().getFullYear()} | Built with
        {` `}
        <a href="https://www.gatsbyjs.org">Gatsby</a>
      </footer>
    </div>
  );
};

export default Layout;
